/* Estilos base para el editor */
.ProseMirror {
  width: 100%;
  padding: 1rem;
  outline: 2px solid transparent;
  outline-offset: 2px;
    min-height: 200px;
}

.ProseMirror > * + * {
  margin-top: 1rem;
}

.ProseMirror p {
  --tw-text-opacity: 1;
  color: rgb(17 24 39 / var(--tw-text-opacity, 1));
}

.ProseMirror ul,
.ProseMirror ol {
  padding-left: 1rem;
}

.ProseMirror ul {
  list-style-type: disc;
}

.ProseMirror ol {
  list-style-type: decimal;
}

.ProseMirror h1 {
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 700;
}

.ProseMirror h2 {
  font-size: 1.25rem;
  line-height: 1.75rem;
  font-weight: 700;
}

.ProseMirror h3 {
  font-size: 1.125rem;
  line-height: 1.75rem;
  font-weight: 700;
}

.ProseMirror blockquote {
  border-left-width: 4px;
  --tw-border-opacity: 1;
  border-color: rgb(229 231 235 / var(--tw-border-opacity, 1));
  padding-left: 1rem;
  font-style: italic;
}

.ProseMirror code {
  border-radius: 0.25rem;
  --tw-bg-opacity: 1;
  background-color: rgb(243 244 246 / var(--tw-bg-opacity, 1));
  padding-left: 0.25rem;
  padding-right: 0.25rem;
}

/* Estilos para el placeholder */
.ProseMirror p.is-editor-empty:first-child::before {
    color: #adb5bd;
    content: attr(data-placeholder);
    float: left;
    height: 0;
    pointer-events: none;
}

:root {
    --background: #ffffff;
    --foreground: #171717;
}

@media (prefers-color-scheme: dark) {
    :root {
        --background: #0a0a0a;
        --foreground: #ededed;
    }
}

body {
    color: var(--foreground);
    background: var(--background);
    font-family: Arial, Helvetica, sans-serif;
}

/* Estilos para el contenido del editor */
.prose {
  max-width: none;
}

.prose p {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.prose h1 {
  margin-top: 1rem;
  margin-bottom: 1rem;
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 700;
}

.prose h2 {
  margin-top: 0.75rem;
  margin-bottom: 0.75rem;
  font-size: 1.25rem;
  line-height: 1.75rem;
  font-weight: 700;
}

.prose h3 {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  font-size: 1.125rem;
  line-height: 1.75rem;
  font-weight: 700;
}

.prose ul,
.prose ol {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
  padding-left: 1rem;
}

.prose ul {
  list-style-type: disc;
}

.prose ol {
  list-style-type: decimal;
}

/* Estilos para el redimensionamiento de im√°genes */
.ProseMirror {
    img {
        &.ProseMirror-selectednode {
            outline: 3px solid #68cef8;
        }

        &.resize-cursor {
            cursor: pointer;
        }
    }
}

.image-resizer {
    display: inline-flex;
    position: relative;
    flex-grow: 0;

    img {
        max-width: 100%;
        height: auto;
    }

    .resize-trigger {
        position: absolute;
        right: -6px;
        bottom: -6px;
        width: 12px;
        height: 12px;
        background-color: #68cef8;
        border-radius: 50%;
        cursor: se-resize;
    }
}

/* Estilos para im√°genes redimensionables */
.ProseMirror img {
    position: relative;
    cursor: default;
    max-width: 100%;
    height: auto;
}

.ProseMirror img.ProseMirror-selectednode {
    outline: 2px solid #68cef8;
}

.resize-handle {
    position: absolute;
    bottom: -4px;
    right: -4px;
    width: 8px;
    height: 8px;
    background-color: #68cef8;
    border-radius: 50%;
    cursor: se-resize;
}

.image-wrapper {
    position: relative;
    display: inline-block;
}

.image-wrapper.resize-active {
    user-select: none;
}

/* Estilos para columnas */
.columns-container {
    display: grid;
    gap: 2rem;
    margin: 1rem 0;
}

.columns-container[data-cols="2"] {
    grid-template-columns: repeat(2, 1fr);
}

.columns-container[data-cols="3"] {
    grid-template-columns: repeat(3, 1fr);
}

@media (max-width: 768px) {
    .columns-container {
        grid-template-columns: 1fr !important;
    }
}

/* Estilos para tablas */
.ProseMirror table {
    border-collapse: collapse;
    margin: 0;
    overflow: hidden;
    table-layout: fixed;
    width: 100%;
    background-color: white;
}

.ProseMirror td,
.ProseMirror th {
    border: none;
    box-sizing: border-box;
    min-width: 1em;
    padding: 0.5rem;
    position: relative;
    vertical-align: top;
}

.ProseMirror th {
    background-color: #f8f9fa;
    font-weight: bold;
}

.ProseMirror .selectedCell:after {
    background: rgba(200, 200, 255, 0.4);
    content: "";
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    pointer-events: none;
    position: absolute;
    z-index: 2;
}

.tableWrapper {
    overflow-x: auto;
}

.resize-cursor {
    cursor: ew-resize;
    cursor: col-resize;
}

